*{
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
  box-sizing: border-box;
}

body {
  background-image:url(/WeatherApp/static/media/bgImage4.56edcce4.jpg);
  background-repeat: repeat;
  background-size: auto;
  

  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

.container{
    margin : 0 auto;
    padding-top: 4rem;
    max-width: 400px;
    min-width: 360px;
    height: 100vh;
    position: relative;
}

.bgcCloud{
    position: fixed;
    width: 100%;
    height: 100%;
    background-image:url(/WeatherApp/static/media/cloud.994c0b2f.png);
    /* background-size: 4000px; */

    -webkit-animation:animate 500s linear infinite;

            animation:animate 500s linear infinite;
}

.bgcOpacity{
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: black;
    opacity: 0.2;
}

/* /////////////////////////// Animation */

@-webkit-keyframes animate 
{
    0%
    {
        background-position: 5440px;
    }

    100%
    {
        background-position: 0px;
    }
}

@keyframes animate 
{
    0%
    {
        background-position: 5440px;
    }

    100%
    {
        background-position: 0px;
    }
}


input{
    display:inline-block;
    width: 44%;
    padding: 10px ;
    font-size: 2.5rem;
    border:none;
    background-color: transparent;
    border-bottom: 2px solid whitesmoke;
    margin-right: 3%;
    margin-left: 3%;
    color: #fff;
    text-shadow: 0 0 1px rgba(0,0,0,.6);
    letter-spacing: 2px;
}


input:focus{
    background-color: transparent;
    outline: none;
    border-bottom: 2px solid #fff;
    color: #fff;
    text-shadow: 0 0 1px rgba(0,0,0,.6);
}

::-webkit-input-placeholder{
    color:#fff;
    
}

::-ms-input-placeholder{
    color:#fff;
    
}

::placeholder{
    color:#fff;
    
}

button{
    width: 70%;
    padding: 20px;
    margin: 10px 15%;
    font-size: 2.5rem;
    background-color: transparent;
    color: #fff;
    border:none;
    outline: none;
    border-bottom: 2px solid whitesmoke;
    color: #fff;
    text-shadow: 0 0 1px rgba(0,0,0,.6);
    letter-spacing: 2px;
    
}

button:hover{
    cursor: pointer;
}

.Output{
    margin-left: 20px;
    line-height: 4rem;
    text-align: left;
    color: #fff;
    text-shadow: 0 0 1px rgba(0,0,0,.6);
    letter-spacing: 1px;
}

.Output strong{
    font-size: 2.5rem;
    font-weight: bold;

}

.Output p{
    font-size: 2rem;
}

.icon{
    text-align: center;
}
